import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    bnbPrice: 0,
    projects: [],
    projectsMap: {},
    wallet: {
        address: undefined,
        chain: undefined,
        type: undefined,
        balances: {}
    }
};

const divvySlice = createSlice({
    name: 'divvy',
    initialState,
    reducers: {

        updateBNBPrice(state, action) {
            state.bnbPrice = parseFloat(action.payload.price);
        },

        updateProjects(state, action) {
            state.projects = action.payload;
        },

        updateProjectsMap(state, action) {
            if (!state.projectsMap[action.payload.contract]) {
                state.projectsMap[action.payload.contract] = {}
            }

            state.projectsMap[action.payload.contract] = action.payload;
        },

        updatePersonalBalance(state, action) {
            if (!state.wallet.balances[action.payload.contract]) {
                state.wallet.balances[action.payload.contract] = 0
            }

            state.wallet.balances[action.payload.contract] = action.payload.balance;
        },

        updateWalletChain(state, action) {
            state.wallet.chain = action.payload
        },

        updateWalletAddress(state, action) {
            state.wallet.address = action.payload
        },

        updateWalletProviderType(state, action) {
            state.wallet.type = action.payload
        },

        resetWallet(state) {
            state.wallet.chain = undefined;
            state.wallet.address = undefined;
            state.wallet.type = undefined;
            state.wallet.balances = {};
        }
    }
});

export const {
    updateBNBPrice,
    updateProjects,
    updateProjectsMap,
    updatePersonalBalance,
    updateWalletChain,
    updateWalletAddress,
    updateWalletProviderType,
    resetWallet
} = divvySlice.actions;

export default divvySlice.reducer;
